.gallery-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 200px;
		transform: translateX(-40%);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 10%;
		right: 0;
		width: 240px;
		transform: translateX(70%) rotate(-25deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.img-gallery {
		height: auto;
		width: 100%;
		aspect-ratio: 16/9;
		object-fit: cover;
	}
	.img-thumbnail {
		object-fit: cover;
		padding: 0;
		border: none;
		border-radius: 0;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
	}
	.splide {
		.splide__slide.is-active {
			.img-thumbnail {
				border: 3px solid #fff;
			}
		}
		.splide__arrow {
			border-radius: 0;
			width: 70px;
			height: 65px;
			background-color: var(--color-secondary);
			@include max-lg {
				width: 40px;
				height: 30px;
			}
			svg {
				fill: var(--text-button);
				width: 25px;
				height: 30px;
				@include max-md {
					width: 15px;
					height: 20px;
				}
			}
			&--prev {
				left: 0;
			}
			&--next {
				right: 0;
			}
		}
	}
}
