.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.elements-wrapper-absolute {
		.background-element {
			&::after {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba($color: #fff, $alpha: 0.3);
				content: "";
			}
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 28%;
		width: 160px;
		transform: translate(-50%, 0);
		pointer-events: none;
		z-index: 2;
		@include max-lg {
			left: 15%;
		}
		@include max-md {
			left: 0;
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -40%);
			@include max-md {
				width: 120px;
				transform: translate(-10%, -40%);
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 28%;
		width: 160px;
		transform: translate(50%, 0);
		pointer-events: none;
		z-index: 2;
		@include max-lg {
			right: 15%;
		}
		@include max-md {
			right: 0;
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(20%, -40%) scaleX(-1);
			@include max-md {
				width: 120px;
				transform: translate(10%, -40%) scaleX(-1);
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-20%, -20%);
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(30%, -20%) scaleX(-1) rotateZ(-10deg);
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translate(-20%, 50%);
		z-index: 2;
		pointer-events: none;
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(10%, 60%);
		pointer-events: none;
		z-index: 2;
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				background: linear-gradient(180deg, var(--color-primary), rgba(255, 255, 255, 0.3));
				border-radius: var(--border-radius);
				color: var(--color-dark);
				min-width: 50%;
				max-width: 100%;
				padding: 3rem 2rem;
				@include max-lg {
					min-width: 70%;
				}
				@include max-md {
					min-width: 100%;
					padding: 2rem 1rem;
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: center;
					z-index: 5;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.greeting-wrapper {
						position: relative;
						@include max-md {
							font-size: 14px;
						}
						h6 {
							font-weight: 700;
						}
					}
					h6 {
						font-weight: 600;
					}
					.title {
						color: var(--color-dark);
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
