.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 80%;
		transform: translate(-30%, 60%);
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 80%;
		transform: translate(30%, 60%) scaleX(-1);
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 30%;
		width: 160px;
		transform: translateX(-50%);
		@include max-lg {
			left: 25%;
		}
		@include max-md {
			left: 0;
			transform: translateX(-20%);
		}
		@include max-xs {
			width: 140px;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -40%);
			@include max-lg {
				width: 130px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 30%;
		width: 160px;
		transform: translateX(50%);
		@include max-lg {
			right: 25%;
		}
		@include max-md {
			right: 0;
			transform: translateX(20%);
		}
		@include max-xs {
			width: 140px;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(20%, -40%) scaleX(-1);
			@include max-lg {
				width: 130px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 180px;
		transform: translate(-50%, 60%);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 100px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 340px;
		transform: translate(-10%, 65%);
		@include max-lg {
			width: 320px;
		}
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 220px;
		transform: translate(-60%, 10%);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 340px;
		transform: translate(10%, 65%);
		@include max-lg {
			width: 320px;
		}
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(50%, 30%) scaleX(-1);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
			transform: translate(60%, 30%) scaleX(-1);
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 70%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.foreground-element {
			width: 100%;
			height: 100%;
			mask-image: url(../images/mask-couple.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: rgba($color: #ffff, $alpha: 0.5);
				filter: blur(5px);
				content: "";
			}
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		.image-element {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 90%;
			height: 90%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask-image: url(../images/mask-couple.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			background: radial-gradient(ellipse, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.5) 40%, transparent 80%);
			border-radius: 500px;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
