.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
		transform: translate(-50%, -5%);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 140px;
			transform: translate(-65%, -5%);
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(65%, -10%) rotate(-25deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
			transform: translate(70%, -20%) rotate(-25deg);
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translate(-50%, -50%);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 260px;
		transform: translate(70%, -50%) rotate(-25deg);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 200px;
			transform: translate(75%, -50%) rotate(-25deg);
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(-28%, 0);
		@include max-lg {
			width: 80px;
		}
		@include max-md {
			width: 60px;
		}
		@include max-xs {
			width: 40px;
		}
		.image-element {
			position: relative !important;
			width: 100% !important;
			inset: 0 !important;
			transform: translate(0) !important;
			mask: unset !important;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100px;
			transform: translate(-40%, -45%);
			@include max-lg {
				width: 80px;
			}
			@include max-md {
				width: 60px;
			}
			@include max-xs {
				width: 40px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(28%, 0);
		@include max-lg {
			width: 80px;
		}
		@include max-md {
			width: 60px;
		}
		@include max-xs {
			width: 40px;
		}
		.image-element {
			position: relative !important;
			width: 100% !important;
			transform: translate(0) !important;
			inset: 0 !important;
			mask: unset !important;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100px;
			transform: translate(40%, -45%) scaleX(-1);
			@include max-lg {
				width: 80px;
			}
			@include max-md {
				width: 60px;
			}
			@include max-xs {
				width: 40px;
			}
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-secondary);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		@include max-md {
			margin-top: 1rem;
			margin-bottom: 8rem;
		}
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 8rem;
	}
	.couple {
		position: relative;
		text-align: center;
		background: var(--color-gradient);
		border-radius: var(--border-radius);
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		padding: 0.5rem;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			margin: -8rem auto 0;
			.foreground-element {
				width: 100%;
				height: 100%;
				mask-image: url(../images/mask-couple.png);
				mask-size: 100% 100%;
				mask-position: top;
				mask-repeat: no-repeat;
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: rgba($color: #ffff, $alpha: 0.5);
					filter: blur(5px);
					content: "";
				}
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
				}
			}
			.image-element {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 90%;
				height: 90%;
				mask-image: url(../images/mask-couple.png);
				mask-size: 100% 100%;
				mask-position: top;
				mask-repeat: no-repeat;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 3rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
