.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	overflow: hidden;
	@include max-lg {
		height: 100vh;
	}
	@include max-xs {
		height: auto;
	}
	.scroll-icon {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, -50%);
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 80%;
		transform: translate(-30%, 50%);
		z-index: 2;
		@include max-lg {
			transform: translate(-20%, 50%);
		}
		@include max-md {
			width: 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 80%;
		transform: translate(30%, 50%) scaleX(-1);
		z-index: 2;
		@include max-lg {
			transform: translate(20%, 50%);
			width: 100%;
		}
		@include max-md {
			width: 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translateX(-50%);
		@include max-lg {
			transform: translateX(-40%);
		}
		@include max-md {
			width: 140px;
			transform: translateX(-50%);
		}
		@include max-xs {
			width: 120px;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -40%);
			@include max-md {
				width: 120px;
				transform: translate(-10%, -40%);
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translateX(50%);
		@include max-lg {
			transform: translateX(40%);
		}
		@include max-md {
			width: 140px;
			transform: translateX(50%);
		}
		@include max-xs {
			width: 120px;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(20%, -40%) scaleX(-1);
			@include max-md {
				width: 120px;
				transform: translate(10%, -40%) scaleX(-1);
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 180px;
		transform: translate(-50%, 60%);
		z-index: 2;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 90px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 340px;
		transform: translate(-10%, 65%);
		z-index: 2;
		@include max-lg {
			width: 320px;
		}
		@include max-md {
			width: 220px;
			transform: translate(-20%, 65%);
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 220px;
		transform: translate(-60%, 10%);
		z-index: 2;
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 340px;
		transform: translate(10%, 65%);
		z-index: 2;
		@include max-lg {
			width: 320px;
		}
		@include max-md {
			transform: translate(30%, 65%);
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(50%, 30%) scaleX(-1);
		z-index: 2;
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 200px;
			transform: translate(55%, 35%) scaleX(-1);
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 80%;
		}
	}
	.orn-12 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, 40%);
		@include max-md {
			width: 140px;
			transform: translate(20%, 15%);
		}
		img {
			transform-origin: 20% 80%;
		}
	}
	.orn-13 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-20%, 40%) scaleX(-1);
		@include max-md {
			width: 140px;
			transform: translate(-20%, 15%) scaleX(-1);
		}
		img {
			transform-origin: 20% 80%;
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		.header {
			position: relative;
			display: grid;
			grid-template-rows: auto 1fr;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				@include max-md {
					width: 100%;
				}
				.foreground-element {
					width: 100%;
					height: 100%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					&::before {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						background-color: rgba($color: #fff, $alpha: 0.5);
						filter: blur(5px);
						content: "";
					}
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						mask-image: url(../images/mask-photo-2.png);
						mask-size: 100% 100%;
						mask-position: top;
						mask-repeat: no-repeat;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
