.reminder-wrap {
	position: relative;
	display: block;
	border-radius: var(--border-radius);
	padding: 8rem 1rem;
	overflow: hidden;
	margin-top: 3rem;
	box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
	@include max-md {
		padding: 3rem 0.75rem;
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 320px;
		transform: translateX(-20%);
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 220px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 320px;
		transform: translateX(20%);
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 220px;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-35%, 60%);
		pointer-events: none;
		z-index: 2;
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(35%, 60%) scaleX(-1);
		pointer-events: none;
		z-index: 2;
	}
	.orn-13 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-20%, 50%) rotate(15deg);
		pointer-events: none;
		z-index: 2;
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		.orn-14 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-40%, -20%);
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
		}
		.orn-15 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-16 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, 50%) rotate(-15deg);
		pointer-events: none;
		z-index: 2;
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		.orn-17 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: translate(30%, -20%) scaleX(-1);
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
		}
		.orn-18 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-40%, 20%) scaleX(-1);
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
