.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-20%, -10%) scaleX(-1);
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 40% 100%;
		}
	}
	.elements-wrapper-absolute.border-custom {
		border-radius: 500px 500px 20px 20px;
		overflow: hidden;
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 2rem 3rem;
		border-radius: 500px 500px 20px 20px;
		box-shadow: inset 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		border: none;
		overflow: hidden;
		@include max-xs {
			padding: 5rem 1rem 2rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
