.story-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translate(-40%, -60%);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			top: 0;
			width: 160px;
			transform: translate(-50%, -20%);
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 40% 100%;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-20%, 40%);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(50%, 0) scaleX(-1);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 160px;
			transform: translate(70%, 70%) scaleX(-1);
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 40% 100%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 260px;
		transform: translate(-50%, -70%) scaleY(-1) rotate(-15deg);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 140px;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 5rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		z-index: 2;
		.foreground-element {
			width: 100%;
			height: 100%;
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: rgba($color: #ffff, $alpha: 0.5);
				filter: blur(5px);
				content: "";
			}
			.story-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
		.image-element {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 95%;
			height: 90%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
