.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-wrapper-absolute {
		.background-element {
			img {
				border-radius: 1000px 1000px 40px 40px;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 8rem 2rem 3rem;
		border-radius: 1000px 1000px 40px 40px;
		box-shadow: inset 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
	}
}
